.wrapper {
  background-color: #003152;
  color: #c7c7c7;
  min-height: 100vh;
}
.content,
.titleContent,
i {
  color: #c7c7c7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
i {
  font-size: 20px !important;
}
.titleContent {
  font-size: 22px;
}
.wrapper .title {
  display: flex;
  place-content: center;
  justify-content: center;
  align-items: center;
  height: 128px;
  background: #002640;
  box-shadow: 0px 0px 5px #171717 inset;
  margin-bottom: 3vh;
}
.block {
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0px 0px;
  margin-bottom: 3vh;
  box-shadow: 1px 1px 2px #1b1b1b;
  background-color: #002f4e;
  border-radius: 10px;
  overflow: hidden;
}
.block .key {
  width: 100%;
  display: flex;
  justify-content: center;
  background: #002742;
  height: 40px;
  align-items: center;
}
.block .value {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 44px;
  font-size: 30px;
}
.lastUpdated {
  display: flex;
  font-size: 10px;
  opacity: 0.6;
  width: 200px;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 20px auto;
}
@media (max-width: 330px) {
  .titleContent {
    font-size: 18px;
  }
}
.dropDownWrap {
  padding-top: 10px;
}
.dropDownWrap .dropdown,
.dropDownWrap .dropdown .menu {
  background: #002138 !important;
  border-color: transparent !important;
}

.dropDownWrap .dropdown .menu {
  background: #142938 !important;
}

.dropDownWrap .dropdown i {
  background-color: transparent !important;
}

.dropDownWrap .dropdown .text {
  color: #c7c7c7 !important;
}

.ui.selection.dropdown .menu > .item {
  border-top: 1px solid #131313 !important;
}

.additionalInfo {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.additionalInfo .block {
  width: 30%;
  margin: 0 8px;
}
.additionalInfo .key,
.additionalInfo .value {
  font-size: 14px;
  height: 30px;
}
